:root {
  --light: #f0f0f0;
  --primary: #292b33;
  --secondary: #d8d7d4;
  --red-color: #ed213a;
}
a {
  text-decoration: none;
}
.text-red-color {
  color: var(--red-color);
}
.text-primary-color {
  color: var(--primary);
}
.text-red-color:hover {
  color: var(--red-color);
}
.w-80 {
  width: 80%;
}
.w-40 {
  width: 40%;
}
.w-30 {
  width: 30%;
}
.w-20 {
  width: 20%;
}
