@import url(https://fonts.googleapis.com/css2?family=Cantarell&display=swap);
:root {
  --light: #f0f0f0;
  --primary: #292b33;
  --secondary: #d8d7d4;
  --red-color: #ed213a;
}
a {
  text-decoration: none;
}
.text-red-color {
  color: #ed213a;
  color: var(--red-color);
}
.text-primary-color {
  color: #292b33;
  color: var(--primary);
}
.text-red-color:hover {
  color: #ed213a;
  color: var(--red-color);
}
.w-80 {
  width: 80%;
}
.w-40 {
  width: 40%;
}
.w-30 {
  width: 30%;
}
.w-20 {
  width: 20%;
}

:root {
  --spacingIcon: 50px;
}
.styleNavbar ul {
  list-style-type: none;
}
.linkNavbar {
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 5px 7px;
  border-radius: 10px 10px 0px 0px;
  position: relative;
  transform: translateY(-0.3rem);
  transition: 0.5s;
  color: var(--red-color);
}
.activeIcon {
  transform: translateY(0.1rem);
  background-color: white;
  color: black;
}
a:hover {
  color: var(--red-color);
}

